.ReactModal__Overlay {
	opacity: 0;
	transition: opacity 200ms ease-in-out;

	&--after-open {
		opacity: 1;
	}

	&--before-close {
		opacity: 0;
	}
}

.modal {
	background: $light-blue;
	color: white;
	max-width: 30rem;
	outline: none;
	padding: $l-size;
	text-align: center;

	&__title {
		margin: 0 0 $m-size 0;
	}

	&__body {
		font-size: 2rem;
		font-weight: 300;
		margin: 0 0 $l-size 0;
		word-break: break-all;
	}

	&__Overlay {
		position: fixed;
		align-items: center;
		display: flex;
		justify-content: center;
		inset: 0;
		background-color: rgba($off-white, 0.75);
	}
}
